z-index: -3;\r\n}\r\n\r\n.hero-bg img {\r\n  width: 100%;\r\n  object-fit: cover;\r\n  background-position: center;\r\n  min-height: 90vh;\r\n  max-height: 100vh;\r\n  margin: auto;\r\n}\r\n\r\n.overlay {\r\n  background-color: #0f3651b2;\r\n  position: absolute;\r\n  width: 100%;\r\n  height: 100%;\r\n  z-index: -2;\r\n}\r\n\r\n.hero h1 {\r\n  font-size: 3rem;\r\n  padding: 1rem 0;\r\n}\r\n\r\n/* Restaurant List */\r\n#resto-list,\r\n.resto-list {\r\n  margin-top: 10%;\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 3rem;\r\n}\r\n\r\n/* Restaurant Card */\r\n#resto-list section,\r\n.resto-list section {\r\n  background: #fcfcfc;\r\n  box-shadow: 5px 5px 60px #bebebe, -5px -5px 60px #ffffff;\r\n  border-radius: 1rem;\r\n  padding: 7%;\r\n  text-align: justify;\r\n  border: 2px solid #0f3551;\r\n  min-width: 200px;\r\n}\r\n\r\nh2 {\r\n  text-align: center;\r\n  margin-top: 1.5rem;\r\n}\r\n\r\n.card-img {\r\n  width: 100%;\r\n  border-radius: 1rem;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n#resto-detail {\r\n  margin-top: 10%;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.resto-main-info {\r\n  background-color: #f6f1cc;\r\n  padding: 1.5rem 1rem 1rem;\r\n  border-radius: 1rem;\r\n  color: #e8745d;\r\n}\r\n\r\n.resto-main-info h3 {\r\n  font-size: 1.75rem;\r\n}\r\n\r\nh4 {\r\n  font-size: 1.25rem;\r\n}\r\n\r\n.resto-full-info {\r\n  margin-top: 1rem;\r\n  text-align: justify;\r\n}\r\n\r\n.resto-full-info h4 {\r\n  margin-top: 1rem;\r\n}\r\n\r\n.resto-full-info ul {\r\n  list-style-position: inside;\r\n}\r\n\r\n.review-container {\r\n  margin: 1rem 0;\r\n}\r\n\r\n#resto-review h4 {\r\n  margin: 1rem 0;\r\n}\r\n#addReviewStatus {\r\n  margin: 1rem 0;\r\n}\r\n\r\n#addReviewStatus span {\r\n  color: #e8745d;\r\n  font-size: 1rem;\r\n}\r\n\r\nlabel {\r\n  color: #0f3551;\r\n}\r\n\r\ninput[type=\"text\"],\r\ntextarea {\r\n  width: 100%;\r\n  min-height: 44px;\r\n  padding: 12px 20px;\r\n  margin: 8px 0;\r\n  display: inline-block;\r\n  border: 1px solid #ccc;\r\n  border-radius: 4px;\r\n  box-sizing: border-box;\r\n}\r\n\r\nbutton[type=\"submit\"] {\r\n  background-color: #e8745d;\r\n  color: #fff;\r\n  min-height: 44px;\r\n  min-width: 44px;\r\n  padding: 1rem 2.5rem;\r\n  border: none;\r\n  border-radius: 10px;\r\n  border: 3px solid #fff;\r\n}\r\n\r\nbutton[type=\"submit\"]:hover {\r\n  background-color: #fff;\r\n  color: #e8745d;\r\n  border: 3px solid #e8745d;\r\n}\r\n\r\n.form-review-name,\r\n.form-review-text {\r\n  display: grid;\r\n  grid-row-gap: 0.5rem;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.review {\r\n  display: grid;\r\n  grid-template-columns: 1fr 5fr;\r\n  column-gap: 1rem;\r\n  margin: 1rem auto;\r\n}\r\n\r\n.review img {\r\n  width: 100%;\r\n}\r\n\r\n.review span {\r\n  color: #e8745d;\r\n  font-size: 1rem;\r\n}\r\n\r\n.review h5 {\r\n  font-size: 1.1rem;\r\n}\r\n\r\n.resto-desc {\r\n  margin-top: 1rem;\r\n}\r\n\r\n.fa-solid {\r\n  margin-right: 0.75rem;\r\n}\r\n\r\n.fa-location-dot {\r\n  color: #ea4b4b;\r\n}\r\n\r\n.fa-star {\r\n  color: #f9e96c;\r\n}\r\n\r\n/*\r\n  Like\r\n*/\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #e8745d;\r\n\r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n\r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\n.search {\r\n  margin-top: 3rem;\r\n}\r\n\r\n/* Footer */\r\n\r\nfooter {\r\n  margin: 1rem 0 0;\r\n  padding: 2rem;\r\n  text-align: center;\r\n  background-color: #0f3551;\r\n  color: #fff;\r\n}\r\n"],"sourceRoot":""}]);
